import Vue from 'vue';

import {
    Message,
    MessageBox,
    Loading,
    Select,
    Option,
    DatePicker,
    Button,
    Pagination,
    Tree,
    Dialog,
    Form,
    FormItem,
    Input,
    RadioGroup,
    Radio,
    Table,
    TableColumn,
    Switch,
    CheckboxGroup,
    Checkbox,
    Tabs,
    TabPane
} from 'element-ui';

import TopBar from './custom/TopBar.vue';
import NavMenu from './custom/NavMenu.vue';
import TitleBox from './custom/TitleBox.vue';

const components = {
    // custom
    TopBar,
    NavMenu,
    TitleBox,
    // element-ui
    // ...
}

Object.keys(components).forEach((key) => {
    Vue.component(key, components[key]);
})

Vue.use(Select);
Vue.use(Option);
Vue.use(DatePicker);
Vue.use(Button);
Vue.use(Pagination);
Vue.use(Tree);
Vue.use(Dialog);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Switch);
Vue.use(CheckboxGroup);
Vue.use(Checkbox);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Loading.directive);

Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$message = Message;
Vue.prototype.$loading = Loading.service;



