import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

const Base = () => import('../views/Base.vue');
const Home = () => import('../views/home/Index.vue'); // 首页
const Quota = () => import('../views/quota/Index.vue'); // 指标
const BasicQuotaType = () => import('../views/quota/subs/BasicQuotaType.vue'); // 基础指标类型
const BasicQuota = () => import('../views/quota/subs/BasicQuota.vue'); // 基础指标
const DeriveQuota = () => import('../views/quota/subs/DeriveQuota.vue'); // 衍生指标
const QuotaSettings = () => import('../views/quota/subs/QuotaSettings.vue'); // 指标配置管理

import store from '../store';
import utils from '../assets/js/common/utils';

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'base',
            component: Base,
            redirect: 'home',
            children: [
                {
                    path: 'home',
                    name: 'home',
                    component: Home,
                    meta: {
                        name: '首页'
                    }
                },
                {
                    path: 'quota',
                    name: 'quota',
                    component: Quota,
                    redirect: 'quota/basicQuotaType',
                    children: [
                        {
                            path: 'basicQuotaType',
                            name: 'BasicQuotaType',
                            component: BasicQuotaType
                        },
                        {
                            path: 'basicQuota',
                            name: 'BasicQuota',
                            component: BasicQuota
                        },
                        {
                            path: 'deriveQuota',
                            name: 'DeriveQuota',
                            component: DeriveQuota
                        },
                        {
                            path: 'quotaSettings',
                            name: 'QuotaSettings',
                            component: QuotaSettings
                        }
                    ]
                }
            ]
        }
    ]
})

const UncheckedRouteNames = [
    'base',
    'home', 'quota'
];

// check auth
router.beforeEach((to, from, next) => {
    utils.setLocalStr('quotaBeforePath', from.path);
    next();
    /*
    if(store.getters.loginStatus) {
      // 已登录
      utils.setLocalStr('quotaBeforePath', from.path);
      next();
    } else {
      if(UncheckedRouteNames.indexOf(to.name) > -1) {
        utils.setLocalStr('quotaBeforePath', from.path);
        next();
      } else {
        next('/login');
      }
    }
    */
});

export default router



