export default {
    regs: {
        phone: /^1[345678]\d{9}$/
    },
    setLocalObj(key, value) {
        value = !!value? value: {};
        window.localStorage.setItem(key, JSON.stringify(value));
    },
    getLocalObj(key) {
        return window.localStorage.getItem(key)? JSON.parse(window.localStorage.getItem(key)) : {};
    },
    setLocalStr(key, value) {
        window.localStorage.setItem(key, value);
    },
    getLocalStr(key) {
        return window.localStorage.getItem(key) || '';
    },
    removeLocal(key) {
        window.localStorage.removeItem(key);
    },
    /**
     * 日期格式转换
     * @param time 待转化的日期，Date类型
     * @param format 格式，默认yyyyMMdd，yyyy-年份 MM-月份 dd-日期 hh-小时 mm-分钟 ss-秒
     */
    convertDate(time, format='yyyy-MM-dd') {
        const y = time.getFullYear();
        const M = time.getMonth()+1 > 9? time.getMonth()+1: '0'+(time.getMonth()+1);
        const d = time.getDate() > 9? time.getDate(): '0'+time.getDate();
        const h = time.getHours() > 9? time.getHours(): '0'+time.getHours();
        const m = time.getMinutes() > 9? time.getMinutes(): '0'+time.getMinutes();
        const s = time.getSeconds() > 9? time.getSeconds(): '0'+time.getSeconds();
        // console.log(time.getA)
        let result = format;
        result = result.replace('yyyy', y);
        result = result.replace('MM', M);
        result = result.replace('dd', d);
        result = result.replace('hh', h);
        result = result.replace('mm', m);
        result = result.replace('ss', s);
        // console.log(result);
        return result;
    }
}



