import * as types from './types'
import http from '@/api/http'

const URL = {
    login: '/user/login',
    logout: '/user/logout',
    userList: '/user/list',
    userInfo: '/user/info',
    userAdd: '/user/add',
    userEdit: '/user/update',
    userAll: '/user/likeList'
}

export const loginAction = ({commit}, data) => {
    return new Promise((resolve, reject) => {
        // http.post(URL.login, data)
        http.realPost(URL.login, data)
            .then(res => {
                commit(types.UPDATE_USER_INFO, Object.assign(res, {
                    username: data.username
                }));
                resolve(res);
            }).catch(e => { reject(e) });
    })
}

export const logoutAction = ({commit}) => {
    return new Promise((resolve, reject) => {
        // http.post(URL.logout, {})
        http.realPost(URL.logout, {})
            .then(res => {
                commit(types.EMPTY_USER_INFO);
                resolve(res);
            }).catch(e => { reject(e) });
    })
}

/***
 * 获取用户列表
 * @param commit
 * @param data
 * @returns {Promise}
 */
export const getUserListAction = ({commit}, data) => {
    return new Promise((resolve, reject) => {
        // http.post(URL.userList, data)
        http.realPost(URL.userList, data)
            .then(res => {
                resolve(res);
            }).catch(e => { reject(e) });
    })
}

/***
 * 获取用户信息
 * @param commit
 * @param data
 * @returns {Promise}
 */
export const getUserInfoAction = ({commit}, data) => {
    return new Promise((resolve, reject) => {
        http.get(URL.userInfo, data)
            .then(res => {
                resolve(res);
            }).catch(e => { reject(e) });
    })
}

