import * as types from './types'
import http from '@/api/http'

const URL = {
    // 基础指标类别
    categoryList: '/category/list', // 列表
    categoryAdd: '/category/add', // 新增
    categoryUpdate: '/category/update', // 修改
    categoryDelete: '/category/delete', // 删除
    // 指标
    quotaList: '/item/list', // 列表
    quotaAdd: '/item/add', // 新增
    quotaUpdate: '/item/update', // 修改
    quotaDelete: '/item/delete', // 删除
    // 组织结构
    orgList: '/org/list', // 组织结构下拉列表
    // 指标结果视图
    resultList: '/indiData/list', // 列表
    // 指标调度配置视图
    scheduleList: '/deployCfg/list', //
    scheduleAdd: '/deployCfg/add', //
    scheduleUpdate: '/deployCfg/update', //
    scheduleDelete: '/deployCfg/delete', //
};

/***
 * 基础指标类别 列表
 * @param commit
 * @param data
 * @returns {Promise}
 */
export const getCategoryListAction = ({commit}, data) => {
    return new Promise((resolve, reject) => {
        http.realPost(URL.categoryList, data)
            .then(res => {
                resolve(res);
            }).catch(e => { reject(e) });
    })
};

