<template>
    <header class="top-bar fn-clear">
        <div class="logo-box fn-clear fn-left">
            <img class="sys-logo fn-left" src="../../assets/images/public/enterprise_logo.jpeg" alt="公司logo">
            <p class="sys-name text-white fn-left">指标管理平台</p>
        </div>
        <div class="user-info-box fn-clear fn-right">
            <p class="logout-box text-white fn-clear fn-right" @click="confirmLogout">
                <i class="icon-logout fn-left"></i>
                <em class="f-s-18 fn-left">退出</em>
            </p>
            <p class="user-name text-white f-s-18 fn-right">{{userInfo.username}}</p>
        </div>
    </header>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex';

    export default {
        name: 'TopBar',
        mixins: [],
        components: {},
        data() {
            return {}
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
        },
        mounted() {
        },
        computed: {
            ...mapGetters([
                'userInfo'
            ])
        },
        methods: {
            ...mapActions([
                'logoutAction'
            ]),
            confirmLogout() {
                this.$confirm('确认退出？', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning'
                }).then(() => {
                    this.logout();
                }).catch((action) => {
                    console.log(action);
                })
            },
            logout() {
                this.logoutAction().then(res => {
                    this.$message.success('退出成功!');
                    this.$router.push('/login');
                }).catch(e => {
                    this.$message.warning('退出失败!');
                })
            }
        },
        filters: {},
        watch: {},
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>
    $top-bg-color: #3c67af;
    $line-height: 31.5px;

    .top-bar {
        height: 75px;
        background-color: $top-bg-color;
    }

    .logo-box {
        width: 500px;
        height: 100%;
        padding-left: 15px;
        .sys-logo {
            position: relative;
            top: 10px;
            display: block;
            height: 55px;
        }
        .sys-name {
            position: relative;
            top: 22px;
            margin-left: 10px;
            font-size: 24px;
            line-height: $line-height;
            font-weight: bold;
        }
    }

    .user-info-box {
        height: 100%;
        padding-right: 5%;
        .logout-box {
            position: relative;
            top: 22px;
            line-height: $line-height;
            cursor: pointer;
            .icon-logout {
                position: relative;
                top: 7px;
                margin-right: 6px;
                width: 16.1px;
                height: 17px;
            }
        }
        .user-name {
            position: relative;
            top: 22px;
            margin-right: 30px;
            line-height: $line-height;
        }
    }
</style>



