import * as types from './types'
import * as actions from './actions'
import * as getters from './getters'

import utils from '@/assets/js/common/utils'

const localData = utils.getLocalObj('monitorUserInfo');
const state = Object.assign({
    username: '',
    token: ''
}, localData);

const mutations = {
    [types.UPDATE_USER_INFO] (state, data) {
        try {
            // update state
            Object.assign(state, data);
            // update localStorage
            utils.setLocalObj('monitorUserInfo', state);
        } catch(e) {
            console.log('存储错误：' + e);
        }
    },
    [types.EMPTY_USER_INFO] (state) {
        try {
            // clear state
            state = Object.assign(state, {
                username: '',
                token: ''
            });
            // clear localStorage
            utils.removeLocal('monitorUserInfo');
        } catch(e) {
            console.log('存储错误：' + e);
        }
    }
}

export default {
    state,
    getters,
    actions,
    mutations
};



