<template>
    <div class="title-box">
        <h1 class="title">{{title}}</h1>
    </div>
</template>

<script>
    export default {
        name: 'TitleBox',
        mixins: [],
        components: {},
        props: {
            title: String
        },
        data() {
            return {}
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {},
        mounted() {},
        computed: {},
        methods: {},
        filters: {},
        watch: {},
        destroyed() {},
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/styles/variables";

    .title-box {
        height: 30px;
        line-height: 30px;
    }
    .title {
        position: relative;
        padding-left: 10px;
        font-size: 20px;
        color: #333333;
        &:before {
            position: absolute;
            top: 2px;
            left: 0;
            display: inline-block;
            content: '';
            width: 4px;
            height: 26px;
            background-color: $blue;
        }
    }
</style>



