import axios from 'axios'
import router from '@/router'
import store from '../store'
const qs = require('qs');
let http = {}

// 设置默认值
axios.defaults.baseURL = baseUrl; // process.env.API;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

// 请求拦截器
axios.interceptors.request.use(function(config) {
    // const token = store.getters.userInfo.token;
    // if(token) {
    //     config.headers.token = token;
    // }
    return config;
}, function(error) {
    console.log(error);
    return Promise.reject(error);
});

// 响应拦截器
axios.interceptors.response.use(function(response) {
    // 拦截code转中文描述
    if (response.data) {
        switch (response.data.code) {
            case 200: // 成功
                break;
            case 100: // 操作失败
                return Promise.reject('操作失败');
            default:
                return Promise.reject(response.data.message); // '正在更新维护中'
        }
    }
    return response ? response.data : {}
}, function(error) {
    // return Promise.reject('正在更新维护中')
    console.log('axios---', error);
    return Promise.reject(error)
});

// post请求
http.post = (url, params, config) => {
    const dataStr = qs.stringify(params, {
        arrayFormat: 'brackets'
    });
    return new Promise((resolve, reject) => {
        axios.post(url, dataStr, config)
            .then((response) => resolve(response.data))
            .catch((error) => reject(error))
    })
};

// get请求
http.get = (url, params) => {
    let p = {};
    p.params = params;

    return new Promise((resolve, reject) => {
        axios.get(url, p)
            .then((response) => resolve(response.data))
            .catch((error) => reject(error))
    })
};

// 调用真实接口 post
http.realPost = (url, params, config) => {
    const dataStr = qs.stringify(params, {
        arrayFormat: 'brackets'
    });
    if(!config) { config= {};}
    config.baseURL = baseUrl;

    return new Promise((resolve, reject) => {
        axios.post(url, dataStr, config)
            .then((response) => resolve(response.data))
            .catch((error) => reject(error))
    })
};

http.nakedPost = (url, params, config) => {
    if(!config) { config= {};}
    config.baseURL = baseUrl;
    return new Promise((resolve, reject) => {
        axios.post(url, params, config)
            .then((response) => resolve(response.data))
            .catch((error) => reject(error))
    })
};

export default http



