<template>
    <nav class="nav-menu">
        <template v-for="item in menus">
            <router-link :to="item.path" class="nav-item" v-if="item.isAuth">
                <i class="iconfont" :class="[item.iconClass]"></i>
                <em class="f-s-16">{{item.name}}</em>
            </router-link>
        </template>
    </nav>
</template>

<script>
    import {mapGetters} from 'vuex';

    export default {
        name: 'NavMenu',
        mixins: [],
        components: {},
        data() {
            return {
                menus: [
                    { iconClass: 'icon-nav_home', path: '/home', name: '首页', isAuth: true },
                    { iconClass: 'icon-nav_media_monitor', path: '/monitor', name: '舆情监测', isAuth: true },
                    { iconClass: 'icon-nav_media_manage', path: '/manage', name: '舆情管理', isAuth: true },
                    { iconClass: 'icon-nav_media_report', path: '/report', name: '舆情报告', isAuth: true },
                    { iconClass: 'icon-nav_media_prealert', path: '/prealert', name: '舆情预警', isAuth: true },
                    { iconClass: 'icon-nav_court', path: '/court', name: '法院信息', isAuth: true },
                    { iconClass: 'icon-nav_setting', path: '/settings', name: '系统设置', isAuth: false },
                ]
            }
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
            if(this.userInfo.role!=="0") {
                this.menus[6].isAuth = true;
            } else {
                this.menus[6].isAuth = false;
            }
        },
        mounted() {
        },
        computed: {
            ...mapGetters([
                'userInfo'
            ])
        },
        methods: {},
        filters: {},
        watch: {},
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>
    $nav-bg-color: #ebeff8;

    .nav-menu {
        width: 240px;
        min-height: 100%;
        background-color: $nav-bg-color;
    }
    .nav-item {
        display: block;
        padding-left: 30px;
        height: 60px;
        line-height: 60px;
        transition: background-color .5s ease;
        /*.iconfont {
            margin-right: 10px;
            font-size: 20px;
            color: #3c67af; // #606060;
        }*/
        &:hover {
            background-color: #dbe6f7; // darken($nav-bg-color, 3%);
        }
        &.router-link-active {
            background-color: #dbe6f7; // darken($nav-bg-color, 5%);
        }
    }

    /** icons start */
    .icon-nav_home {
        position: relative;
        top: 2px;
        width: 20px;
        height: 19px;
    }
    .icon-nav_media_monitor, .icon-nav_court {
        position: relative;
        top: 1px;
        width: 20px;
        height: 16px;
    }
    .icon-nav_media_manage, .icon-nav_media_report {
        position: relative;
        top: 1.5px;
        width: 20px;
        height: 19.5px;
    }
    .icon-nav_media_prealert {
        position: relative;
        top: 3px;
        width: 20px;
        height: 20px;
    }
    .icon-nav_setting {
        position: relative;
        top: 2px;
        width: 20px;
        height: 19.45px;
    }
    /** icons end */
</style>



