import utils from './utils';
import Vue from 'vue';

const filters = {
    convertDate: (val, format='yyyy-MM-dd') => {
        return utils.convertDate(val, format);
    }
}

Object.keys(filters).forEach((key) => {
    Vue.filter(key, filters[key]);
})



